<template>
  <div class="order-info main-container">
    <!--  订单头部  -->
    <section>
      <h6 class="title">订单详情</h6>
      <el-form>
        <el-form-item label="订单编号">{{ orderData?.orderNo }}</el-form-item>
        <el-form-item label="客户名称">{{
          orderData?.companyName
        }}</el-form-item>
        <el-form-item label="下单时间">{{
          orderData?.createdTime
        }}</el-form-item>
        <el-form-item>{{ orderData?.helpOrderName ?? "未知" }}</el-form-item>
      </el-form>
    </section>
    <!-- 订单完成情况 -->
    <section class="order-situation">
      <h6 class="title success">
        <el-tag :type="orderData?.orderStateCdStyleType">
          {{ orderData?.orderStateCdName ?? "未知" }}
        </el-tag>
      </h6>
      <el-form label-width="100px">
        <el-form-item label="订单总价"
          >&yen;&nbsp;{{ orderData?.total ?? "0.00" }}</el-form-item
        >
        <el-form-item label="优惠金额"
          >&yen;&nbsp;{{ orderData?.couponPrice ?? "0.00" }}</el-form-item
        >
        <el-form-item label="实收金额"
          >&yen;&nbsp;{{ orderData?.actualPaid ?? "0.00" }}</el-form-item
        >
      </el-form>
    </section>
    <!-- 产品列表 -->
    <section class="table-container">
      <h6 class="title">产品列表</h6>
      <div class="product-list">
        <div class="item item-header">
          <div class="th">产品名称</div>
          <div class="th">产品规格</div>
          <div class="th">合计</div>
          <!--          <div class="th">操作</div>-->
        </div>
        <div class="item item-body">
          <div
            :class="[
              initData.expanded ? 'tr-content tr-expanded' : 'tr-content',
            ]"
            v-for="(item, index) in productList"
            :key="index"
          >
            <div class="tr">
              <div class="td-content">
                <div
                  v-if="item.augmentList.length"
                  class="expanded"
                  @click="handleExpanded"
                >
                  <span class="expanded-button"></span>
                </div>
                <div class="td">{{ item.combineServiceName }}</div>
                <div class="td">{{ item.specsInfo }}</div>
                <div class="td">{{ item.actualPaid }}</div>
              </div>
              <div class="augment-list" v-show="initData.expanded">
                <div
                  class="td-content"
                  v-for="(augmentItem, augmentIndex) in item.augmentList"
                  :key="augmentIndex"
                >
                  <div class="td">{{ augmentItem.combineServiceName }}</div>
                  <div class="td">{{ augmentItem.specsInfo }}</div>
                  <div class="td">{{ augmentItem.actualPaid }}</div>
                </div>
              </div>
            </div>
          </div>
          <TableEmpty v-if="!productList.length" elHeight="200px"></TableEmpty>
        </div>
      </div>
      <div class="amount">
        总金额：<span>{{ orderData?.actualPaid ?? "0.00" }}</span
        >元
      </div>
    </section>
    <!-- 付款 -->
    <section>
      <h6 class="title">付款</h6>
      <el-form>
        <el-form-item label="订单金额"
          >&yen;&nbsp;{{ paymentData?.total ?? "0.00" }}</el-form-item
        >
        <el-form-item label="优惠总金额"
          >&yen;&nbsp;{{
            paymentData?.undertakeDiscount ?? "0.00"
          }}</el-form-item
        >
        <el-form-item label="承担优惠"
          >&yen;&nbsp;{{ paymentData?.planCommission ?? "0.00" }}</el-form-item
        >
        <el-form-item label="实际返佣"
          >&yen;&nbsp;{{
            paymentData?.actualCommission ?? "0.00"
          }}</el-form-item
        >
        <el-form-item label="订单实际金额"
          >&yen;&nbsp;{{ paymentData?.realityPrice ?? "0.00" }}</el-form-item
        >
        <el-form-item label="实际支付金额"
          >&yen;&nbsp;{{ paymentData?.actualPaid ?? "0.00" }}</el-form-item
        >
      </el-form>
    </section>
    <div
      class="button-group"
      v-if="orderData?.orderStateCd === 'NP' && orderData?.helpOrder !== 0"
    >
      <el-button type="primary" @click="handleFastPay">立即付款</el-button>
    </div>
  </div>
  <EditPriceDialog
    ref="editPriceRef"
    @emitFunc="getOrderDetail"
  ></EditPriceDialog>
</template>
<script lang="ts" setup>
import { ref, reactive, defineAsyncComponent } from "vue";

import { LocalStorage, SessionStorage } from "jz-tool-lib";
import { useVueRouter } from "@assets/useHook";

import { customerOrderDetailApi } from "@api/order-management-api";

const EditPriceDialog = defineAsyncComponent(
  () =>
    import(
      "@/components/order-management/customer-order/detail/edit-price-dialog.vue"
    )
);

defineOptions({
  name: "orderManagement.customerOrder.detail",
});
const props = defineProps<{
  orderId: string;
}>();

const editPriceRef = ref();
const { goPage } = useVueRouter();
const { bd_userInfo: userInfo } = LocalStorage.batchGetItem(["bd_userInfo"]);
const initData = reactive({
  expanded: false,
  editPriceVisible: false,
});
const orderData = ref<Record<string, any>>({});
const paymentData = ref<Record<string, any>>({});
const productList = ref<Record<string, any>[]>([]);
const handleFastPay = () => {
  const { orderNo, companyId, actualPaid, paymethodCd } = orderData.value;
  let modePayment = "";
  if (["WX", "ZFB"].includes(paymethodCd)) {
    modePayment = "onlinePayment";
  } else if (["DLSZHYE", "DLSYHK"].includes(paymethodCd)) {
    modePayment = "balancePayment";
  }
  SessionStorage.setItem("bd_market-payment", {
    sumPrice: actualPaid,
    companyId: companyId,
    modePayment: modePayment,
    paymentType: paymethodCd,
    profileId: userInfo.profileId,
  });
  goPage(`/payment/vip-buy/${orderNo}`);
};
const getOrderDetail = async () => {
  try {
    const { data } = await customerOrderDetailApi.getData(props.orderId);
    const { orderInfo, listInfo, paymentInfo } =
      customerOrderDetailApi.handleData(data);
    orderData.value = orderInfo;
    productList.value = listInfo;
    paymentData.value = paymentInfo;
  } catch (e) {
    console.log(e);
  }
};
function handleExpanded() {
  initData.expanded = !initData.expanded;
}
getOrderDetail();
</script>
<style lang="scss" scoped>
.order-info {
  .el-form {
    display: flex;
    flex-wrap: wrap;
    margin-top: 20px;
    padding-left: 10px;
    .el-form-item {
      flex: 0 0 25%;
    }
  }
}
section {
  margin-bottom: 20px;
  padding: 36px 24px 20px;
  background-color: #ffffff;
}
.order-situation {
  display: flex;
  .title {
    flex: 0 0 50%;
    display: flex;
    align-items: center;
    padding-left: 50px;
    &.success {
      color: #0056ff;
    }
    &.danger {
      color: #f56c6c;
    }
    &.info {
      color: #909399;
    }
  }
  .el-form {
    display: initial;
    margin-top: 0;
  }
}
.title {
  font-size: 16px;
  font-weight: 900;
  color: #242934;
  line-height: 1;
}
.amount {
  display: flex;
  justify-content: flex-end;
  margin-top: 30px;
  font-size: 15px;
  color: #606266;
  span {
    color: #f12525;
    font-weight: 900;
  }
}

.product-list {
  margin-top: 20px;
  .item-header {
    display: flex;
    border-bottom: 1px solid #ebeef5;
    .th {
      flex: 1;
      color: #242934;
      font-weight: 900;
      font-size: 14px;
      padding: 18px 15px;
      text-align: center;
      //&:last-child {
      //  flex: 0 0 200px;
      //}
    }
  }
  .item-body {
    .tr-content {
      display: flex;
      min-height: 74px;
      overflow: hidden;
      &.tr-expanded {
        .expanded .expanded-button {
          transform: rotateZ(45deg);
        }
      }
      .tr {
        // flex: 0 0 calc(100% - 200px);
        flex: 0 0 100%;
        @include flex(initial, initial, column);
      }
      .td-content {
        flex: 1;
        display: flex;
        position: relative;
        .expanded {
          position: absolute;
          top: 50%;
          left: 0;
          @include flex(center, center);
          width: 15px;
          height: 15px;
          cursor: pointer;
          transform: translateY(-50%);
          &-button {
            width: 8px;
            height: 8px;
            border-right: 1px solid #606266;
            border-bottom: 1px solid #606266;
            transform: rotateZ(-45deg);
            transform-origin: center;
            transition: transform 0.2s ease;
          }
        }
      }
      .td {
        flex: 1;
        @include flex(center, center);
        color: #606266;
        font-size: 14px;
        padding: 18px 15px;
        text-align: center;
      }
      .option-group {
        @include flex(center, center);
        width: 200px;
        padding: 18px 15px;
      }
    }
  }
}

.button-group {
  display: flex;
  justify-content: flex-end;
}
</style>
